export default {
	data() {
		return {
			id: '',
			title: 'Hello',
			uni_input: '请输入运单号',
		}
	},
	onLoad() {

	},
	methods: {
		search: function (e) {
			uni.navigateTo({
				url: '../result/result?id='+this.id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		onKeyIdInput: function (e) {
			this.id = e.target.value;
		},
		qrcode: function () {
			uni.scanCode({
				success: function (res) {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
